* {
  padding: 0;
  margin: 0; }

html {
  font-size: 0.625rem;
  background: #e9e9e9; }

body {
  min-height: calc(100vh - 1rem); }

.main-wrapper {
  width: 90rem;
  margin: auto;
  padding-bottom: 3rem; }

@-webkit-keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes fadein {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@-webkit-keyframes fadeout {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes fadeout {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@-webkit-keyframes moveInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10rem);
            transform: translateX(-10rem); }
  80% {
    -webkit-transform: translateX(1rem);
            transform: translateX(1rem); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0); } }

@keyframes moveInLeft {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10rem);
            transform: translateX(-10rem); }
  80% {
    -webkit-transform: translateX(1rem);
            transform: translateX(1rem); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0); } }

@-webkit-keyframes moveInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(10rem);
            transform: translateX(10rem); }
  80% {
    -webkit-transform: translateX(-1rem);
            transform: translateX(-1rem); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0); } }

@keyframes moveInRight {
  0% {
    opacity: 0;
    -webkit-transform: translateX(10rem);
            transform: translateX(10rem); }
  80% {
    -webkit-transform: translateX(-1rem);
            transform: translateX(-1rem); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0); } }

@-webkit-keyframes moveInTop {
  0% {
    opacity: 0.5;
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem); }
  80% {
    -webkit-transform: translateY(0.3rem);
            transform: translateY(0.3rem); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0); } }

@keyframes moveInTop {
  0% {
    opacity: 0.5;
    -webkit-transform: translateY(-0.5rem);
            transform: translateY(-0.5rem); }
  80% {
    -webkit-transform: translateY(0.3rem);
            transform: translateY(0.3rem); }
  100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0); } }

body {
  font-size: 1.6rem;
  font-family: "Lato", sans-serif; }

.App-title {
  padding: 3rem;
  text-align: center;
  font-size: 5.2rem;
  font-weight: 300;
  color: #666666; }

.heading-primary {
  margin-top: 1.6rem;
  color: #33b5e5;
  font-size: 2.2rem;
  font-weight: 700; }

li {
  padding-top: 0.8rem; }

/* input box */
input[type="text"],
select {
  margin: 0.4rem 0;
  display: inline-block;
  border: 0.1rem solid #ccc;
  border-radius: 0.4rem; }

input:required {
  border: 0.1rem solid #ffa5a5;
  border-radius: 0.4rem; }

/* summary */
.tasks-list {
  padding-left: 3rem;
  text-align: left;
  list-style-position: inside;
  padding-bottom: 4.2rem; }
  .tasks-list-gap {
    padding: 0.6rem; }

.summary-info {
  line-height: 2.4rem;
  margin-bottom: 1.2rem; }

.text-totalHours {
  display: inline-block;
  color: #227796; }
  .text-totalHours-enter {
    -webkit-animation: moveInTop 0.4s;
            animation: moveInTop 0.4s; }
  .text-totalHours-exit {
    display: none; }

.center {
  text-align: center; }

.center button {
  font-size: 1.6rem; }

.wrapper {
  display: grid;
  grid-gap: 0.2rem;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: auto;
  grid-template-areas: "App-title App-title App-title App-title" "guide guide guide guide" "form form form form " "summary summary summary summary"; }

.App-title {
  grid-area: App-title; }

.guide {
  grid-area: guide;
  padding: 0 1rem 1rem;
  justify-self: right; }

.form-list {
  grid-area: form;
  border-radius: 0.3rem;
  border-top: 0.5rem solid #33b5e5;
  background: white;
  -webkit-box-shadow: 0 0 1rem #888888;
          box-shadow: 0 0 1rem #888888;
  margin: 1rem; }
  .form-list--search {
    -webkit-animation: moveInRight 0.3s;
            animation: moveInRight 0.3s; }
  .form-list--report {
    -webkit-animation: moveInLeft 0.3s;
            animation: moveInLeft 0.3s; }

.summary {
  grid-area: summary;
  border-radius: 0.3rem;
  border-top: 0.5rem solid skyblue;
  background: white;
  -webkit-box-shadow: 0 0 1rem #888888;
          box-shadow: 0 0 1rem #888888;
  text-align: center;
  padding: 1rem;
  margin: 1rem; }
  .summary--search {
    -webkit-animation: moveInRight 0.5s;
            animation: moveInRight 0.5s; }
  .summary--report {
    -webkit-animation: moveInLeft 0.5s;
            animation: moveInLeft 0.5s; }

.summary-appear {
  -webkit-animation: moveInRight 0.5s;
          animation: moveInRight 0.5s; }

.summary button {
  font-size: 1.6rem; }

/* form setting */
.task-edit {
  padding: 2rem;
  background-color: white;
  margin: auto;
  line-height: 3rem; }

.task-edit span {
  padding: 0.5rem;
  padding-left: 4rem;
  padding-right: 2rem;
  display: inline-block;
  vertical-align: top;
  width: 35%;
  text-align: right; }

.task-edit input,
select {
  padding: 0.5rem;
  min-height: 2.5rem;
  display: inline-block;
  width: 35%; }

/* button setting */
button,
input[type="submit"] {
  padding: 0.5rem;
  margin-left: 1rem;
  background: none;
  border-radius: 3px;
  font-size: 1.1rem;
  -webkit-transition: all 0.1s;
  -o-transition: all 0.1s;
  transition: all 0.1s;
  position: relative;
  z-index: 2; }

button:after,
input[type="submit"]:after {
  content: "";
  z-index: -1;
  display: block;
  background: #227796;
  position: absolute;
  width: 100%;
  height: 0;
  left: 0;
  top: 0;
  -webkit-transition: all 0.1s;
  -o-transition: all 0.1s;
  transition: all 0.1s; }

button:hover,
input[type="submit"]:hover,
button:focus,
input[type="submit"]:focus {
  color: #fff;
  outline: 0; }

button:hover:after,
input[type="submit"]:hover:after,
button:focus:after,
input[type="submit"]:focus:after {
  height: 100%; }

.summary button {
  background: #33b5e5;
  color: white; }

.form-list--report button,
.form-list--search button {
  background: #33b5e5;
  color: white; }

.taskSelector button {
  font-size: 1.6rem;
  display: inline-block;
  border-radius: 0;
  width: 50%;
  margin: 0;
  padding: 0.6rem; }

.guide button {
  font-size: 1.6rem; }

.btn-search {
  border: 0.1rem solid #227796; }

.btn-back {
  border: 0.1rem solid #227796; }

.guide button {
  font-size: 1.6rem; }

/* button setting end */
/* Popup component styling*/
.mm-popup {
