  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  overflow: auto;
  background: rgba(0, 0, 0, 0.3); }

.mm-popup__close {
  position: absolute;
  top: 1.5rem;
  right: 2rem;
  padding: 0;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  outline: none;
  text-align: center;
  border-radius: 1rem;
  border: none;
  text-indent: -9999px;
  background: transparent url("data:image/png;base64,") no-repeat center center;
  background-size: 100%;
  margin: 0; }

.mm-popup__input {
  display: block;
  width: 100%;
  height: 3rem;
  border-radius: 0.3rem;
  background: #f5f5f5;
  border: 0.1rem solid #e9ebec;
  outline: none;
  -webkit-box-sizing: border-box !important;
  box-sizing: border-box !important;
  font-size: 1.4rem;
  padding: 0 1.2rem;
  color: #808080; }

.mm-popup__btn {
  border-radius: 0.3rem;
  -webkit-box-sizing: border-box;
