.mm-popup__btn--success {
  background-color: #27ae60;
  border-color: #27ae60;
  color: #fff; }

.mm-popup__btn--danger {
  background-color: #c5545c;
  border-color: #c5545c;
  color: #fff; }

.mm-popup__box {
  width: 34rem;
  text-align: center;
  position: fixed;
  top: 23%;
  left: 50%;
  margin-left: -17.5rem;
  background: #fff;
  -webkit-box-shadow: 0px 0.5rem 2rem 0px rgba(126, 137, 140, 0.2);
          box-shadow: 0px 0.5rem 2rem 0px rgba(126, 137, 140, 0.2);
  border-radius: 0.5rem;
  border: 0.1rem solid #b8c8cc;
  overflow: hidden;
  z-index: 1001; }

.mm-popup__box__header {
