  border-bottom-right-radius: 0.4rem;
  z-index: 2; }

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none; }

.react-autosuggest__suggestion {
  cursor: pointer;
  padding: 1rem 2rem; }

.react-autosuggest__suggestion--highlighted {
  background-color: #ddd; }

/* autosuggestion end  */



